.woocommerce-input-field-template {
  height: 40px;
  border: 1px solid rgba(51, 51, 51, 0.5);
  padding-left: 15px;
  -webkit-border-radius: 5px !important;
  -moz-border-radius: 5px !important;
  border-radius: 5px !important;
}

.transit {
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}

@media screen and (min-width: 415px) {
  .home .buurthaven__logo {
    max-width: 800px;
  }
}

.buurthaven__logo {
  max-width: 140px;
}

.buurthaven__logo-footer {
  width: 140px;
}

.logo__link {
  margin: auto;
}

header[data-sticky=header].fixme {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 999;
  background: rgba(255, 255, 255, 0.2);
  height: 104px;
}

header[data-sticky=header].fixme .logo__link {
  margin: 0;
  margin-left: 2rem;
}

header[data-sticky=header].fixme .logo__link .buurthaven__logo {
  width: 140px;
}

footer[data-template] {
  background: var(--green);
}

.main.header_fixed {
  padding-top: 294px;
}

body.custom-background:not(.home) {
  background-image: none !important;
}

body.custom-background:not(.home) .main.header_fixed {
  padding-top: 117px;
}

.bg-yellow {
  background-color: var(--yellow);
}

.special-big-title {
  font-size: 54px;
  font-weight: bold;
  font-style: italic;
}

.social__blocks {
  position: fixed;
  top: 45%;
  right: 20px;
}

.social__blocks a[class*=social__blocks__link-],
.social__blocks a[class^=social__blocks__link-] {
  background: var(--white);
  height: 40px;
  width: 40px;
}

.menu-container {
  display: none;
  background: var(--green);
  opacity: 0;
  position: absolute;
}

.menu-container.active {
  display: flex;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  opacity: 1;
  z-index: 9999;
}

.menu__button-container {
  position: fixed;
  top: 20px;
  right: 40px;
}

.menu-close__button {
  position: absolute;
  top: 20px;
  right: 40px;
}

.hover-color-dark-dark-10 {
  color: #1d2124 !important;
}

.evenement .evenement__img {
  object-fit: cover;
  width: 100%;
  height: 700px;
}

.evenement .datum {
  font-size: 29px;
}

.evenement .evenemt__title-block {
  background: var(--pink);
  color: var(--blue);
  padding: 30px 20px;
  text-transform: uppercase;
}

.evenement .evenemt__title-block h1 {
  font-weight: bold;
}

@media screen and (min-width: 415px) {
  .evenement .evenemt__title-block {
    margin-left: -110px;
    width: 220px;
    text-align: center;
    margin-top: 40px;
    margin-bottom: 40px;
  }
}

.frontpage__agenda-block {
  height: 400px;
  background-size: cover;
  background-position: center center;
}

